import * as React from "react";
import {FC} from "react";

interface Props {
    alt: string;
    code: string;
    size?: number;
}

export const Emoji: FC<Props> = ({ code, alt, size = 16 }) => <img
    style={{ width: size, height: size }}
    className={'inline'}
    alt={alt}
    src={`https://cdn.jsdelivr.net/gh/twitter/twemoji@14.0.2/assets/72x72/${code}.png`}
/>
