import {atom, selector} from "recoil";
import {todaysCountyState} from "../counties";

export const guessesState = atom<(string|null)[]>({
    key: 'guesses',
    default: [null, null, null, null, null, null],
});

export const currentGuessState = atom<{ value: string; label: string; }|null>({
    key: 'currentGuess',
    default: null,
});

export const usedAllGuessesState = selector({
    key: 'usedAllGuessesState',
    get: ({ get }) => typeof get(guessesState).find(g => g === null) === 'undefined',
});

export const succeededTodayState = selector({
    key: 'succeededTodayState',
    get: ({ get }) => {
        const allGuesses = get(guessesState);
        const todaysCounty = get(todaysCountyState);

        return allGuesses.includes(todaysCounty?.id!);
    },
});
