export const getResultsClipboardText = (date: string, guesses: (string | null)[], todaysCounty: County): string => {
    if (todaysCounty === null) {
        return 'Error, please report';
    }

    let guessEmojis = '';
    guesses.map(g => {
        if (g === null) {
            guessEmojis += '⬜️';

            return;
        }

        if (g === todaysCounty.id) {
            guessEmojis += '🟩';

            return;
        }

        guessEmojis += '🟥';
    })

    return `Countydle (${date})\n${guessEmojis}\nhttps://leightonthomas.github.io`;
}
