/**
 * @author https://www.movable-type.co.uk/scripts/latlong.html
 */
export const distance = (start: [string, string], end: [string, string]) => {
    const lat1 = Number(start[0]);
    const lat2 = Number(end[0]);

    const lon1 = Number(start[1]);
    const lon2 = Number(end[1]);

    const R = 6371e3; // metres
    const phi1 = lat1 * Math.PI/180; // phi, lambda in radians
    const phi2 = lat2 * Math.PI/180;
    const deltaPhi = (lat2-lat1) * Math.PI/180;
    const deltaLambda = (lon2-lon1) * Math.PI/180;

    const a = Math.sin(deltaPhi/2) * Math.sin(deltaPhi/2) +
        Math.cos(phi1) * Math.cos(phi2) *
        Math.sin(deltaLambda/2) * Math.sin(deltaLambda/2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));

    return R * c; // meters
};

/**
 * Only calculates initial bearing but should be fine given we're only within the UK here rather than global
 *
 * @author https://www.movable-type.co.uk/scripts/latlong.html
 */
export const bearing = (start: [string, string], end: [string, string]) => {
    const lat1 = Number(start[0]) * Math.PI/180;
    const lat2 = Number(end[0]) * Math.PI/180;

    const lon1 = Number(start[1]) * Math.PI/180;
    const lon2 = Number(end[1]) * Math.PI/180;

    const y = Math.sin(lon2-lon1) * Math.cos(lat2);
    const x = Math.cos(lat1)*Math.sin(lat2) -
        Math.sin(lat1)*Math.cos(lat2)*Math.cos(lon2-lon1);
    const theta = Math.atan2(y, x);

    return (theta*180/Math.PI + 360) % 360; // in degrees;
}
