import {useRecoilState, useRecoilValue} from "recoil";
import {countiesState, todaysCountyState} from "../../model/state/counties";
import * as React from "react";
import {useEffect, useMemo, useRef, useState} from "react";
import {get, set} from "idb-keyval";
import produce from "immer";
import {Emoji} from "../emoji";
import {BodyButton, Button} from "../button";
import Select, {SelectInstance} from "react-select";
import {UnusedGuess, UsedGuess} from "../guess";
import {getResultsClipboardText} from "../../util/results";
import {
    currentGuessState,
    guessesState,
    succeededTodayState,
    usedAllGuessesState
} from "../../model/state/stages/countyGuessing";
import {DaySave, MetaSave} from "../../model/save";
import {todaysDateState, yesterdaysDateState} from "../../model/state/date";

export const CountyGuess = () => {
    const [currentGuess, setCurrentGuess] = useRecoilState(currentGuessState);
    const [guesses, setGuesses] = useRecoilState(guessesState);
    const usedAllGuesses = useRecoilValue(usedAllGuessesState);
    const succeededToday = useRecoilValue(succeededTodayState);
    const todaysCounty = useRecoilValue(todaysCountyState);
    const todaysDate = useRecoilValue(todaysDateState);
    const yesterdaysDate = useRecoilValue(yesterdaysDateState);
    const counties = useRecoilValue(countiesState);
    const selectRef = useRef<SelectInstance<{ value: string, label: string }>|null>(null);

    const [loaded, setLoaded] = useState(false);

    const countyOptions = useMemo(
        () => counties.map(c => ({ value: c.id, label: c.name })).sort((a, b) => a.label.localeCompare(b.label)),
        [counties],
    );

    useEffect(() => {
        get<DaySave>(todaysDate).then(data => {
            if (typeof data === 'undefined') {
                setLoaded(true);

                return;
            }

            setGuesses(data.county.guesses);
            setLoaded(true);
        });
    }, []);

    const openGuessSelect = () => {
        const current = selectRef.current;
        if (current === null) {
            return;
        }

        current.focus();
        current.openMenu('first');
    };

    const addGuess = () => {
        const nextIndex = guesses.findIndex(v => v === null);
        if (nextIndex === -1) {
            return;
        }

        if (currentGuess === null) {
            return;
        }

        const newGuesses = produce(guesses, draft => {
            draft[nextIndex] = currentGuess.value;
        });
        const wasSuccess = newGuesses.includes(todaysCounty.id);

        setGuesses(newGuesses);
        setCurrentGuess(null);

        // update today's record
        get<DaySave>(todaysDate).then(todaysData => {
            let newData: DaySave = {
                county: { guesses: newGuesses },
                neighbours: {guesses: []},
                population: {guesses: []},
                success: wasSuccess,
            };
            if (typeof todaysData !== 'undefined') {
                newData = produce(todaysData as DaySave, draft => {
                    draft.county.guesses = newGuesses;
                    draft.success = wasSuccess;
                });
            }

            set(todaysDate, newData);
        });

        // and if they won, update the meta record
        if (wasSuccess) {
            get<MetaSave>("meta").then(existingMetaSave => {
                let streak = 1;
                if ((typeof existingMetaSave !== 'undefined') && existingMetaSave.lastWinDate === yesterdaysDate) {
                    streak = existingMetaSave.streak + 1;
                }

                set("meta", { streak, lastWinDate: todaysDate });
            });
        }
    };

    const map = <svg style={{ height: 150, transform: 'matrix(1,0,0,1.7,-0.40705784,0)' }} className={'w-full mt-10 mb-10'} viewBox={todaysCounty.shape.viewbox}>
        <g><path d={todaysCounty.shape.path} style={{ fill: '#464646', fillRule: 'evenodd' }} /></g>
    </svg>;

    let bottomSection: JSX.Element;
    if (succeededToday) {
        bottomSection = <>
            <div className={'flex flex-col w-full border-2 justify-center text-center items-center border-green-300 p-5 space-y-1.5'}>
                <div className={'w-full text-xs uppercase tracking-widest'}>Correct!</div>
                <div className={'w-full text-center text-3xl font-bold uppercase tracking-widest'}>{todaysCounty.name}</div>
                <div className={'w-full justify-center text-sm flex flex-row space-x-1'}>
                    <Emoji size={12} alt={'Party face'} code={'1f973'} />
                    <Emoji size={12} alt={'Party popper'} code={'1f389'} />
                    <Emoji size={12} alt={'Party face'} code={'1f973'} />
                </div>
            </div>
            <div className={'flex flex-row w-full justify-between space-x-10'}>
                <BodyButton
                    onClick={() => navigator.clipboard.writeText(getResultsClipboardText(todaysDate, guesses, todaysCounty))}
                    body={
                        <div className={'flex flex-row w-full space-x-1 justify-center items-center'}>
                            <Emoji size={20} alt={'Clipboard'} code={'1f4cb'} />
                            <span className={'text-xl font-medium text-black uppercase tracking-widest'}>Copy Results</span>
                        </div>
                    }
                />
                {/*<BodyButton*/}
                {/*    theme={'blue'}*/}
                {/*    onClick={() => {}}*/}
                {/*    body={*/}
                {/*        <div className={'flex flex-row w-full space-x-1 justify-center items-center'}>*/}
                {/*            <Emoji size={20} alt={'Star'} code={'2b50'} />*/}
                {/*            <span className={'text-xl font-medium text-black uppercase tracking-widest'}>*/}
                {/*                Challenge #1*/}
                {/*            </span>*/}
                {/*        </div>*/}
                {/*    }*/}
                {/*/>*/}
            </div>
        </>;
    } else if (usedAllGuesses) {
        bottomSection = <div className={'flex flex-col w-full border-2 justify-center text-center items-center border-red-300 p-5 space-y-1.5'}>
            <div className={'w-full text-xs uppercase tracking-widest'}>Incorrect</div>
            <div className={'w-full text-center text-3xl font-bold uppercase tracking-widest'}>{todaysCounty.name}</div>
            <div className={'w-full text-xs uppercase tracking-widest'}>
                Try again tomorrow <Emoji size={16} alt={'Pensive face'} code={'1f614'} />
            </div>
        </div>;
    } else if (loaded) {
        bottomSection = <>
            <Select
                isDisabled={! loaded}
                value={currentGuess}
                onChange={setCurrentGuess}
                options={countyOptions}
                ref={selectRef}
            />
            <Button onClick={loaded ? addGuess : (() => {})} text={'Guess'} />
        </>;
    } else {
        bottomSection = <></>;
    }

    return <>
        <div className={'flex w-full flex-col space-y-1.5 p-2'}>
            {map}
            {guesses.map((guess, index) => guess === null
                ? <UnusedGuess onClick={openGuessSelect} key={index} />
                : <UsedGuess key={index} guess={guess} />
            )}
        </div>
        <div className={'flex w-full flex-col space-y-1.5 p-2'}>
            {bottomSection}
        </div>
    </>;
};
