import {atom, selector, selectorFamily} from "recoil";
import * as data from './../../../data.json';
import {todaysDateState} from "./date";

export const countiesState = atom<County[]>({
    key: 'counties',
    default: (() => {
        const counties = [];
        for (let i = 0; i < data.counties.length; i++) {
            counties.push(data.counties[i]);
        }

        return counties;
    })() as unknown as County[],
});

export const dayCountiesState = atom<{ [key: string]: string }>({
    key: 'dayCounties',
    default: data.days as { [key: string]: string },
});

export const todaysCountyState = selector<County>({
    key: 'county',
    get: ({ get }) => {
        const today = get(dayCountiesState)[get(todaysDateState)];
        const found = get(countiesState).find(c => c.id === today);

        if (typeof found === 'undefined') {
            // TODO add tests that test the data.json to ensure this never happens in production
            throw new Error(`Unrecognised county for today: "${today}"`);
        }

        return found;
    },
});

export const getCountyState = selectorFamily<County|null, string>({
    key: 'getCounty',
    get: (id: string) => ({ get,  }) => {
        const found = get(countiesState).find(c => c.id === id);
        if (typeof found === 'undefined') {
            return null;
        }

        return found;
    },
});
