import {selector} from "recoil";
import {getDate} from "../../util/date";

const todaysDate = new Date();
const yesterdaysDate = new Date();
yesterdaysDate.setDate(todaysDate.getDate() - 1);

export const todaysDateState = selector({
    key: 'todaysDate',
    get: () => getDate(todaysDate),
});

export const yesterdaysDateState = selector({
    key: 'yesterdaysDate',
    get: () => getDate(yesterdaysDate),
});
