import * as React from "react";
import {FC, useMemo} from "react";
import {useRecoilValue} from "recoil";
import {bearing, distance} from "../util/distance";
import {getCountyState, todaysCountyState} from "../model/state/counties";
import {Emoji} from "./emoji";

type UsedGuessProps = {
    guess: string;
};

type UnusedGuessProps = {
    onClick: () => void;
};

export const UnusedGuess: FC<UnusedGuessProps> = ({ onClick }) => <div
    onClick={onClick}
    className={'bg-gray-200 rounded w-full'}
    style={{ height: 30 }}
/>;

export const UsedGuess: FC<UsedGuessProps> = ({ guess }) => {
    const todaysCounty = useRecoilValue(todaysCountyState);
    const guessCounty = useRecoilValue(getCountyState(guess));
    const isCorrect = todaysCounty.id === guessCounty?.id;

    const actualDistance = useMemo(
        () => (distance(todaysCounty.coordinates, guessCounty?.coordinates!) / 1000)
            .toLocaleString(
                'en-GB',
                { style: 'unit', unit: 'kilometer', unitDisplay: 'narrow', maximumFractionDigits: 2 }
            ),
        [],
    );

    const actualBearing = useMemo(
        () => bearing(guessCounty?.coordinates!, todaysCounty.coordinates),
        [],
    );

    let direction: JSX.Element;

    if (isCorrect) {
        direction = <Emoji alt={'Correct'} code={'1f7e2'} />;
    } else {
        direction = <Emoji alt={`Unknown - ${actualBearing}`} code={'26ab'} />;
        if ((actualBearing >= 340) || (actualBearing <= 20)) {
            direction = <Emoji alt={'North'} code={'2b06'} />;
        } else if ((actualBearing > 150) && (actualBearing <= 200)) {
            direction = <Emoji alt={'South'} code={'2b07'} />;
        } else if ((actualBearing > 250) && (actualBearing <= 290)) {
            direction = <Emoji alt={'West'} code={'2b05'} />;
        } else if ((actualBearing > 70) && (actualBearing <= 110)) {
            direction = <Emoji alt={'East'} code={'27a1'} />;
        } else if ((actualBearing > 110) && (actualBearing <= 150)) {
            direction = <Emoji alt={'South-east'} code={'2198'} />;
        } else if ((actualBearing > 200) && (actualBearing <= 250)) {
            direction = <Emoji alt={'South-west'} code={'2199'} />;
        } else if ((actualBearing > 290) && (actualBearing < 340)) {
            direction = <Emoji alt={'North-west'} code={'2196'} />;
        } else if ((actualBearing > 20) && (actualBearing <= 70)) {
            direction = <Emoji alt={'North-east'} code={'2197'} />;
        }
    }

    return <div className={'flex flex-row space-x-1'} style={{ height: 30 }}>
        <div className={`uppercase tracking-widest flex items-center border-2 pl-2 pr-2 rounded flex-1 justify-center ${isCorrect ? 'border-green-300' : 'border-red-300'}`}>{guessCounty?.name}</div>
        <div className={`flex items-center font-normal border-2 pl-2 pr-2 rounded flex-none justify-center ${isCorrect ? 'border-green-300' : 'border-red-300'}`} style={{ width: 91 }}>{actualDistance}</div>
        <div className={`flex items-center font border-2 pl-2 pr-2 rounded flex-none justify-center ${isCorrect ? 'border-green-300' : 'border-red-300'}`} style={{ width: 36 }} data-test={actualBearing}>
            {direction}
        </div>
    </div>;
};
