import * as React from "react";
import {FC, ReactNode} from "react";

type Theme = 'normal'|'blue'|'red';

type AlwaysRequiredProps = {
    onClick: () => void;
};

type OptionalProps = {
    theme?: Theme;
};

type TextProps = AlwaysRequiredProps & OptionalProps & { text: string; };
type BodyProps = AlwaysRequiredProps & OptionalProps & { body: ReactNode; };

export const Button: FC<TextProps> = (props) => <BodyButton
    {...props}
    body={<span className={'text-xl font-medium text-black uppercase tracking-widest'}>{props.text}</span>}
/>;

export const BodyButton: FC<BodyProps> = ({ onClick, body, theme = 'normal' }) => <div
    onClick={onClick}
    className={`flex w-full flex-col rounded border-2 justify-center text-center items-center hover:cursor-pointer ${getColors(theme)}`}
    style={{height: 38}}
>{body}</div>;

const getColors = (theme: Theme): string => {
    switch (theme) {
        default:
        case 'normal':
            return 'border-gray-300 hover:bg-gray-100 active:bg-gray-300';
        case 'blue':
            return 'border-blue-300 hover:bg-blue-100 active:bg-blue-300';
        case 'red':
            return 'border-red-300 hover:bg-red-100 active:bg-red-300';
    }
}
